// 全局设置
const gbs = {
    // 接口根地址。本地代理可在/config/index.js -> proxyTable中配置
    /*
     *  proxyTable: {
     *    '/static/data/': {
     *      target: 'http://xxxxxx.com',
     *      changeOrigin: true,
     *      pathRewrite: {
     *        '^/static/data/': ''
     *      }
     *    }
     *  },
     * */
    host: 'http://192.168.17.50:808',
    // 本地存储的key
    db_prefix: 'sx_admin_',
  },
  /* 状态码 */
  STATUS_CODES = {
    201: {
      msg: '提交成功',
      action: () => {
      }
    },
    202: {
      msg: '等待后台处理中',
      action: () => {
      }
    },
    204: {
      msg: '删除成功',
      action: () => {
      }
    },
    400: {
      msg: '请求错误',
      action: () => {
      }
    },
    401: {
      msg: '用户名或密码错误',
      action: function () {
        this.data.password = '';
      }
    },
    403: {
      msg: '访问被禁止',
      action: () => {
      }
    },
    404: {
      msg: '请求资源不存在',
      action: () => {
      }
    },
    406: {
      msg: '请求数据格式不存在',
      action: () => {
      }
    },
    410: {
      msg: '请求资源已被永久删除',
      action: () => {
      }
    },
    422: {
      msg: '提交格式验证错误',
      action: () => {
      }
    },
    500: {
      msg: '服务器未响应',
      action: () => {
      }
    }
  },
  // 回调设置
  cbs = {
    /**
     * ajax请求成功，返回的状态码不是200时调用
     * @param  {object} err 返回的对象，包含错误码和错误信息
     */
    statusError(err) {
      if (Object.keys(STATUS_CODES).includes(err.status + '')) {
        this.$message({
          showClose: true,
          message: STATUS_CODES[err.status].msg,
          type: 'error'
        });
        STATUS_CODES[err.status].action.call(this);
      }
      else {
        this.$message({
          showClose: true,
          message: '出现未配置的错误信息,请检查控制台!',
          type: 'error'
        });
        console.log(err)
      }
    },

    /**
     * ajax请求网络出错时调用
     */
    requestError(err, url) {
      this.$message({
        showClose: true,
        message: `请求${url}时出错!`,
        type: 'error'
      });
    }
  };

export {gbs, cbs}



